<template lang="pug">
  div.wx-index(style="text-align:center")
    img(src="../assets/images/poster/poster03.png" width="auto" height="auto")
</template>
<script>
export default {
  name: 'Poster',
  data () {
    return {
    }
  },
  components: {
  }
}
</script>

<style lang="less" rel="stylesheet/less">
  .wx-index{
    width: 100%;
    .upload-file-content{
      padding-top: 32px;
      .title{
        font-family: 'PingFangSC-Medium';
        font-size: 24px;
        color: #4A4A4A;
        margin-bottom: 16px;
      }
      .upload-content{
        width: 100%;
        height: 320px;
        border: 1px dotted #B1C5D8;
        border-radius: 4px;
        text-align: center;
        position: relative;
        background-color: #FFF;
        .tips-one{
          margin-top: 95px;
          .icon-upload-file{
            display: inline-block;
            width: 30px;
            height: 30px;
            background: url('../assets/images/icon-upload-file.png') no-repeat;
            background-size: 30px 30px;
            cursor: pointer;
          }
          .icon-upload-excel{
            display: inline-block;
            width: 53px;
            height: 64px;
            background: url('../assets/images/icon-upload-excel.png') no-repeat;
            background-size: 53px 64px;
            cursor: pointer;
          }
        }
        .tips-two{
          margin-top: 26px;
          font-size: 16px;
          color: #4A4A4A;
          .btn-upload-input{
            text-decoration: underline;
            cursor: pointer;
          }
        }
        .tips-three{
          margin-top: 8px;
          font-size: 14px;
          color: #9B9B9B;
        }
        .upload-input{
          display: none;
        }
        .file-name{
          font-size: 18px;
          color: #4A4A4A;
          margin-top: 24px;
        }
        .el-icon-close{
          position: absolute;
          font-size: 14px;
          top: 20px;
          right: 20px;
          cursor: pointer;
        }
      }
    }
    .btn-content{
      margin-top: 46px;
      text-align: center;
      .el-button{
        width: 320px;
        height: 48px;
        text-align: center;
        line-height: 48px;
        background-image: linear-gradient(-270deg, #FF9105 0%, #FFCC02 100%);
        border-radius: 24px;
        font-family: 'PingFangSC-Semibold';
        font-size: 18px;
        color: #FFFFFF;
        border: none;
        padding: 0;
      }
    }
  }
</style>



